.mainSec {
    padding: 5% 3%;
}

.couponBox {
    margin-top: 2%;
    padding: 1% 3%;
    background-color: rgba(245, 245, 245, 0.514);
}

.couponBox .css-k50mv2-MuiTypography-root {
    font-weight: 400 !important;
}

.applyBox {
    display: flex;
    gap: 25px;
    align-items: center;
}

.billingDetails {
    margin-top: 2%;
    border: 1px solid rgba(136, 136, 136, 0.329);
    padding: 1%;
    height: min-content;
}

.yourOrder {
    margin-top: 2%;
    padding: 1%;
}

.productBox {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 3% 3%;
}

.productBox img {
    min-width: 25%;
    max-width: 25%;
}

.text .css-1g4dizw-MuiTypography-root {
    font-weight: 500 !important;
}

.orderSummary {
    margin-top: 2%;
    padding: 3% 3%;
    border: 1px solid rgba(136, 136, 136, 0.329);
}

.payMethod {
    margin-top: 5%;
    border: 1px solid rgba(136, 136, 136, 0.329);
    padding: 5%;
}

.orderButton {
    padding: 3% 2%;
}

.productStack {
    max-height: 400px;
    overflow-y: scroll;
    overflow-x: hidden;
}


/* // media query  */

@media screen and (max-width:800px) {
    .mainSec {
        padding-top: 10% !important;
    }
}